.sliderElement {
  border-radius: 10px;

  padding: 30px 30px;
  margin-right: 20px;
  width: 550px !important;
  background-color: white;
}

.info {
  display: flex;
  gap: 10px;
  align-items: center;
}
.startText {
  width: 500px;
}

.sliderThumb {
  border-radius: 100%;
  width: 55px;
  height: 55px;
}
.username {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 30px;
}
.content {
  margin-top: 20px;
}
.date::before {
  margin-top: 10px;
  margin-bottom: 20px;

  width: 100%;
  border-bottom: 2px solid #032541;
  content: "";
  display: block;
}

.button {
  color: black;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
}
